function errorLog(err: any) {
    // // 
    // store.dispatch('d2admin/log/add', {
    //     type: 'error',
    //     err,
    //     info: ''
    // })
    // // 
    // if (process.env.NODE_ENV === 'development') {
    //     // util.log.danger('>>>>>> Error >>>>>>')
    //     console.log(err)
    // }
    // 
    Message({
        message: err.message,
        type: 'error',
        duration: 5 * 1000
    })
}

//  axios 
const service = axios.create({
    baseURL: process.env.VUE_APP_API,
    timeout: 5000 // 
})

// 
service.interceptors.request.use(
    config => {
        // 
        // const token = util.cookies.get('token')
        // token-- ['X-Token']key 
        // config.headers['token'] = token
        return config
    },
    error => {
        // 
        console.log(error)
        Promise.reject(error)
    }
)

// 
service.interceptors.response.use(
    response => {
        // dataAxios  axios  data
        const dataAxios = response.data
        // 
        const {code} = dataAxios
        //  code 
        if (code === undefined) {
            //  code 
            return dataAxios
        } else {
            //  code  
            switch (code) {
                case 200:
                    // [  ] code === 0 
                    return dataAxios.data
                case 'xxx':
                    // [  ]  code
                    // errorCreate(`[ code: xxx ] ${dataAxios.msg}: ${response.config.url}`)
                    break
                default:
                    //  code
                    // errorCreate(`${dataAxios.msg}: ${response.config.url}`)/
                    break
            }
        }
    },
    error => {
        if (error && error.response) {
            switch (error.response.status) {
                case 400:
                    error.message = '';
                    break
                case 401:
                    error.message = '';
                    break
                case 403:
                    error.message = '';
                    break
                case 404:
                    error.message = `: ${error.response.config.url}`;
                    break
                case 408:
                    error.message = '';
                    break
                case 500:
                    error.message = '';
                    break
                case 501:
                    error.message = '';
                    break
                case 502:
                    error.message = '';
                    break
                case 503:
                    error.message = '';
                    break
                case 504:
                    error.message = '';
                    break
                case 505:
                    error.message = 'HTTP';
                    break
                default:
                    break
            }
        }
        errorLog({err: error})
        return Promise.reject(error)
    }
)

export default service
